import { ImagePicker, WingBlank } from 'antd-mobile';
import React, { Component } from 'react'
import { Menu, Dropdown, Button, Row, Col, Form, Input, Upload, InputNumber, Breadcrumb, Alert } from 'antd';
import { Icon } from 'antd-mobile'
const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;
const FormItem = Form.Item;
// 添加奖品

class addObj extends Component {
  constructor(props) {
    super(props);
    this.state = {
      obj: '',
      count: '',
      probability: '',
      img: '',
      files: []

    }
  }
  setUserInfo(event, key) {
    let obj = {};
    obj[key] = event.target.value;
    this.setState(obj);
  }
  onChange = (files, type, index) => {
    let fname = '';
    var time = new Date();
    let year = time.getFullYear();
    let month = time.getMonth();
    let day = time.getDate();
    let hours = time.getHours();
    let minutes = time.getMinutes();
    let second = time.getSeconds();
    fname = `${year}-${month}-${day}-${hours}-${minutes}-${second}.jpg`;

    console.log(fname);

    this.setState({
      files,
      img: 'image/object/' + fname
    });
    console.log(files[0]);
    if (files[0]) {
      fetch('https://www.sevenhero.cn:2050/uploadobject', {
        method: 'POST',
        body: JSON.stringify({
          filename: fname,
          url: files[0].url,

        })
      }).then(res => {
        // alert('上传成功');
        // if(res.status===200){
        //   this.setState({
        //     files:[]
        //   })
        // }
      })
      // }
    }
    else {
      alert('记得上传图片');
    }
  }

  putData = () => {
    let data = {
      obj: this.state.obj,
      count: this.state.number,
      probability: this.state.probability,
      img: this.state.img,
    };
    console.log(data)

    console.log(data);
    fetch('https://www.sevenhero.cn:2050/object', {
      method: 'POST',
      headers: {
        'content-type': 'text/plain'
      },
      body: JSON.stringify(data)
    }).then(async res => {

      if (!res.ok) {
        let errtext = await res.text()
        throw new Error(`${res.status} : ${errtext}`)//错误的状态码和内容
      } else {
        //  return res//成功后的返回值
        return;
      }

    }).then(d => {
      //'提交成功'
      alert('奖品添加成功')

      this.setState({
        obj: '',
        number: '',
        probability: '',
        src: '',
        files: []

      })

    }).catch(err => {
      alert(err.message)
    })
  }
  getInitialState = () => {
    return {
      current: '1',
      openKeys: [],
    };
  }
  handleClick = (e) => {
    console.log('click ', e);
    this.setState({
      current: e.key,
      openKeys: e.keyPath.slice(1),
    });
  }
  onToggle = (info) => {
    this.setState({
      openKeys: info.open ? info.keyPath : info.keyPath.slice(1),
    });
  }
  addName = (e) => {
    this.setState({
      obj: e.target.value
    })
  }
  addNumber = (e) => {
    this.setState({
      number: e
    })
  }
  addPro = (e) => {
    this.setState(
      {
        probability: e
      }
    )
  }
  alterPsd=()=>{
    var name = prompt("请输入密码", ""); //将输入的内容赋给变量 name ，
    console.log(name)
    fetch('https://www.sevenhero.cn:2050/upadm', {
      method: 'POST',
      headers: {
        'content-type': 'text/plain'
      },
      body: JSON.stringify(name)
    }).then(async res => {

      if (!res.ok) {
        let errtext = await res.text()
        throw new Error(`${res.status} : ${errtext}`)
      } else {
        //  return res//成功后的返回值
      }

    }).then(d => {
      //'提交成功'
      localStorage.removeItem('token')
      localStorage.removeItem('userinfo')
      this.props.history.push('/login');

    }).catch(err => {
      alert('错误')
    })  
  }
  render() {
    const username = JSON.parse(localStorage.getItem('userinfo')).username

    const { files } = this.state;
    const menu = (
      <Menu onClick={this.handleMenuClick}>
        <Menu.Item key="1">
        <a className="user_left"
          size='small'
            style={{
              margin:10,
              padding:5
            }}
            onClick={() => {
              if (window.confirm('确定退出？')) {
                localStorage.removeItem('token')
                localStorage.removeItem('userinfo')
                localStorage.removeItem('state')

                this.props.history.push('/login');

              }
            }}
          >退出账号</a>
          
        </Menu.Item>
        <Menu.Item>
        <a className="user_left"
            style={{
              margin:10,
              padding:5

            }}
            size="small"
            onClick={() => this.alterPsd()}

          >修改密码</a> 
        </Menu.Item>

      </Menu>
    );
    return (
      <div>
        <Row type="flex">
          <Col xs={2} sm={2} md={4} lg={4} justify="start" id='user' style={{ color: 'blue', fontSize: '22px', textShadow: '5' }}>
            天刀DKP后台管理系统
            </Col>
          <Col xs={{ span: 4, offset: 14 }} sm={{ span: 4, offset: 14 }} md={{ span: 4, offset: 14 }} lg={{ span: 4, offset: 16 }} justify="end">
            <Dropdown.Button size='large' onClick={this.handleButtonClick} overlay={menu} type="ghost">
             {username}
              </Dropdown.Button>
          </Col>
        </Row>
        <Row>
          <Col span='4'>
            <Menu onClick={this.handleClick}
              // style={{ width: 240 }}
              defaultOpenKeys={['sub2']}
              selectedKeys={['/addobj']}
              mode="inline"
            >
              <SubMenu key="sub1" title={<span><Icon type="mail" /><span>用户管理</span></span>}>
                <Menu.Item key="/user"
                  onClick={() => {
                    this.props.history.push('/user')
                  }}
                >用户列表</Menu.Item>
                <Menu.Item key="/adduser"
                  onClick={() => {
                    this.props.history.push('/adduser')
                  }}
                >添加用户</Menu.Item>
              </SubMenu>
              <SubMenu key="sub2" title={<span><Icon type="appstore" /><span>奖品管理</span></span>}>
                <Menu.Item key="/object"
                  onClick={() => {
                    this.props.history.push('/object')
                  }}
                >奖品列表</Menu.Item>
                <Menu.Item key="/addobj"
                  onClick={() => {
                    this.props.history.push('/addobj')
                  }}
                >添加奖品</Menu.Item>
                <Menu.Item key="/alterFen"
                  onClick={() => {
                    this.props.history.push('/alterFen')
                  }}
                >积分消耗</Menu.Item>
              </SubMenu>
              <SubMenu key="sub3" title={<span><Icon type="setting" /><span>获奖信息</span></span>}>
                <Menu.Item key="/ownobj"
                  onClick={() => {
                    this.props.history.push('/ownobj')
                  }}
                >获奖信息</Menu.Item>
              </SubMenu>
              <SubMenu key="sub4" title={<span><Icon type="setting" /><span>管理员管理</span></span>}>
                <Menu.Item key="/adminList"
                  onClick={() => {
                    this.props.history.push('/adminList')
                  }}
                >管理员列表</Menu.Item>
                <Menu.Item key="/addAdmin"
                  onClick={() => {
                    this.props.history.push('/addAdmin')
                  }}
                >添加管理员</Menu.Item>
              </SubMenu>
            </Menu>
          </Col>
          <Col span='20'>
            <Breadcrumb>
              <Breadcrumb.Item>奖品管理</Breadcrumb.Item>
              <Breadcrumb.Item><a href="/addobj">添加奖品</a></Breadcrumb.Item>
            </Breadcrumb>
            <Alert message="最多设置12个奖品" type="info" closeText="不再提醒" width='100' />
            <Form horizontal='true' style={{ marginTop: '5%' }}>
              <FormItem
                id="control-input"
                label="奖品名称"
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 4 }}
              >
                <Input id="control-input" placeholder=""
                  onChange={(e) => { this.addName(e) }}
                  value={this.state.obj}
                />
              </FormItem>
              <FormItem
                id="control-input"
                label="奖品数量"
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 14 }}
              >
                <InputNumber id="control-input" placeholder=""
                  onChange={(e) => { this.addNumber(e) }}
                  value={this.state.number}
                  min={0}
                />
              </FormItem>
              <FormItem
                id="control-input"
                label="奖品概率"
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 14 }}

              >
                <InputNumber id="control-input" placeholder=""
                  onChange={(e) => { this.addPro(e) }}
                  value={this.state.probability}
                  min={0}
                  max={100}
                />
              </FormItem>
              <FormItem
                label="奖品图片"
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 14 }}

              >
                {/* <Upload name="logo" action="/upload.do" listType="picture"  files={files} onChange={this.onChange}
                // {...getFieldProps('upload', {
                //   valuePropName: 'fileList',
                //   normalize: this.normFile,
                // })}
                >
                  <Button type="ghost">
                    <Icon type="upload" /> 点击上传
            </Button>
                </Upload> */}
                <ImagePicker
                  files={files}
                  onChange={this.onChange}
                  onImageClick={(index, fs) => console.log(index, fs)}
                  selectable={files.length < 1}
                  multiple={this.state.multiple}
                />
              </FormItem>
              <FormItem wrapperCol={{ span: 4, offset: 6 }} style={{ marginTop: 24 }}>
                <Button size='large' type="primary" htmlType="submit"
                  onClick={() => {
                    //  console.log(this.state);
                    this.putData();
                  }}
                >添加</Button>
              </FormItem>
            </Form>
          </Col>
        </Row>

      </div>
    )
  }
}
export default addObj;