import React, { Component } from 'react'
import { Tabs, Select } from 'antd';
import { Menu, Dropdown, Table, Row, Col ,Breadcrumb} from 'antd';
import { Icon, Grid, Button } from 'antd-mobile';
const TabPane = Tabs.TabPane;
const SubMenu = Menu.SubMenu;
// 获得奖品
class spOwnobj extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ownobj: [],
      // username:'1111'
    }
  }
  getData = () => {
    fetch('https://www.sevenhero.cn:2050/ownobj').then(res => {
      if (!res.ok) {
        throw new Error(`${res.status} : ${res.statusText}`)
      } else {
        return res.json()
      }
    }).then(d => {
      console.log(d);
      this.setState({
        ownobj: d
      })
      console.log(this.state.ownobj)
    }).catch(err => {
      console.error(err)
    })
  }
  componentDidMount = () => {//在render执行之后就执行，且只执行一次
    this.getData();
    // console.log(localStorage.getItem('userinfo').username)
    // this.setState({
    //   username:localStorage.getItem('userinfo').username
    // })
  }
  getInitialState = () => {
    return {
      current: '1',
      openKeys: [],
    };
  }
  handleClick = (e) => {
    console.log('click ', e);
    this.setState({
      current: e.key,
      openKeys: e.keyPath.slice(1),
    });
  }
  onToggle = (info) => {
    this.setState({
      openKeys: info.open ? info.keyPath : info.keyPath.slice(1),
    });
  }
  
  render() {
    const username = JSON.parse(localStorage.getItem('userinfo')).username

    const pagination = {
      total: this.state.ownobj.length,
      showSizeChanger: true,
      showTotal(total){ return `共 ${total} 条`},
      onShowSizeChange(current, pageSize) {
        console.log('Current: ', current, '; PageSize: ', pageSize);
      },
      onChange(current) {
        console.log('Current: ', current);
      },
    };
    const menu = (
      <Menu onClick={this.handleMenuClick}>
        <Menu.Item key="1">
          <Button className="user_left"
            style={{
              marginRight: 0
            }}
            onClick={() => {
              if (window.confirm('确定退出？')) {
                localStorage.removeItem('token')
                localStorage.removeItem('userinfo')
                localStorage.removeItem('state')

                this.props.history.push('/login');

              }
            }}
          >退出账号</Button>
        </Menu.Item>
      </Menu>
    );
    return (
      <div>
         <Row type="flex">
            <Col xs={2} sm={2} md={4} lg={4} justify="start" id='user' style={{ color: 'blue', fontSize: '22px',textShadow:'5' }}>
              天刀DKP后台管理系统
            </Col>
            <Col xs={{ span: 4, offset: 14 }} sm={{ span: 4, offset: 14 }} md={{ span: 4, offset: 14 }} lg={{ span: 4, offset: 16 }} justify="end">
              <Dropdown.Button  size='large' onClick={this.handleButtonClick} overlay={menu} type="ghost">
               {username}
              </Dropdown.Button>
            </Col>
          </Row>
        <Row>
          <Col span='4'>
            <Menu onClick={this.handleClick}
              defaultOpenKeys={['sub3']}
              selectedKeys={['spownobj']}
              mode="inline"
            >
              <SubMenu key="sub1" title={<span><Icon type="mail" /><span>用户管理</span></span>}>
                <Menu.Item key="spuser"
                  onClick={() => {
                    this.props.history.push('/spuser')
                  }}
                >用户列表</Menu.Item>
                <Menu.Item key="spadduser"
                  onClick={() => {
                    this.props.history.push('/spadduser')
                  }}
                >添加用户</Menu.Item>
              </SubMenu>
              <SubMenu key="sub2" title={<span><Icon type="appstore" /><span>奖品管理</span></span>}>
                <Menu.Item key="spobject"
                  onClick={() => {
                    this.props.history.push('/spobject')
                  }}
                >奖品列表</Menu.Item>
                <Menu.Item key="spaddobj"
                  onClick={() => {
                    this.props.history.push('/spaddobj')
                  }}
                >添加奖品</Menu.Item>
                 <Menu.Item key="/alterFen"
                    onClick={() => {
                      this.props.history.push('/alterFen')
                    }}
                  >积分消耗</Menu.Item>
              </SubMenu>
              <SubMenu key="sub3" title={<span><Icon type="setting" /><span>获奖信息</span></span>}>
                <Menu.Item key="spownobj"
                  onClick={() => {
                    this.props.history.push('/spownobj')
                  }}
                >获奖信息</Menu.Item>
              </SubMenu>
            </Menu>
          </Col>
          <Col span="20">
            <Breadcrumb>
              <Breadcrumb.Item>获奖信息</Breadcrumb.Item>
              <Breadcrumb.Item><a href="/spownobj">获奖信息</a></Breadcrumb.Item>
            </Breadcrumb>
            <Table
              columns={
                [
                  { title: '获奖用户', width: 100, dataIndex: 'gamename', key: 'gamename' },
                  { title: '奖品名字', width: 100, dataIndex: 'obj', key: 'obj' },
                  { title: '获奖时间', dataIndex: 'time', key: 'time', width: 150 },
                ]
              }
              dataSource={this.state.ownobj}
              rowKey={(record,index) => record.obj+index}
              // scroll={{ x: 1500, y: 500 }}
              pagination={pagination}
            />
          </Col>
        </Row>





      </div>
    )
  }
}
export default spOwnobj;