import React, { Component } from 'react'
import { Tabs, Select } from 'antd';
import { Menu, Dropdown, Table, Row, Col, Breadcrumb } from 'antd';
import { Icon, Grid, Button } from 'antd-mobile';
const TabPane = Tabs.TabPane;
const SubMenu = Menu.SubMenu;
const Option = Select.Option;
// 奖品

class adminList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      object: [],
      port: 'https://www.sevenhero.cn:2050/static/'
    }
  }
  getData = () => {
    fetch('https://www.sevenhero.cn:2050/addTo').then(res => {
      if (!res.ok) {
        throw new Error(`${res.status} : ${res.statusText}`)
      } else {
        return res.json()
      }
    }).then(d => {
      console.log(d);
      this.setState({
        object: d
      })
      // console.log(this.state.user)
    }).catch(err => {
      console.error(err)
    })
  }

  delData = (e) => {
    if (window.confirm('确定删除？')) {
      let data = {
        username: e
      }

      console.log(data);
      fetch('https://www.sevenhero.cn:2050/deladm', {
        method: 'POST',
        body: JSON.stringify(data)
      }).then(async res => {

        if (!res.ok) {
          let errtext = await res.text()
          throw new Error(`${res.status} : ${errtext}`)
        } else {
          //  return res//成功后的返回值
        }

      }).then(d => {
        //'提交成功'
        alert('删除成功')
        this.getData();

      }).catch(err => {
        alert('错误')
      })
    }
  }
  componentDidMount = () => {//在render执行之后就执行，且只执行一次
    this.getData();
  }
  getInitialState = () => {
    return {
      current: '1',
      openKeys: [],
    };
  }
  handleClick = (e) => {
    console.log('click ', e);
    this.setState({
      current: e.key,
      openKeys: e.keyPath.slice(1),
    });
  }
  onToggle = (info) => {
    this.setState({
      openKeys: info.open ? info.keyPath : info.keyPath.slice(1),
    });
  }
  alterPsd = () => {
    var name = prompt("请输入密码", ""); //将输入的内容赋给变量 name ，
    console.log(name)
    fetch('https://www.sevenhero.cn:2050/upadm', {
      method: 'POST',
      headers: {
        'content-type': 'text/plain'
      },
      body: JSON.stringify(name)
    }).then(async res => {

      if (!res.ok) {
        let errtext = await res.text()
        throw new Error(`${res.status} : ${errtext}`)
      } else {
        //  return res//成功后的返回值
      }

    }).then(d => {
      //'提交成功'
      localStorage.removeItem('token')
      localStorage.removeItem('userinfo')
      this.props.history.push('/login');

    }).catch(err => {
      alert('错误')
    })
  }
  render() {
    const username = JSON.parse(localStorage.getItem('userinfo')).username

    const pagination = {
      total: this.state.object.length,
      showSizeChanger: true,
      showTotal(total) { return `共 ${total} 条` },
      onShowSizeChange(current, pageSize) {
        console.log('Current: ', current, '; PageSize: ', pageSize);
      },
      onChange(current) {
        console.log('Current: ', current);
      },
    };
    const menu = (
      <Menu onClick={this.handleMenuClick}>
        <Menu.Item key="1">
          <a className="user_left"
            size='small'
            style={{
              margin: 10,
              padding: 5
            }}
            onClick={() => {
              if (window.confirm('确定退出？')) {
                localStorage.removeItem('token')
                localStorage.removeItem('userinfo')
                localStorage.removeItem('state')
                this.props.history.push('/login');

              }
            }}
          >退出账号</a>

        </Menu.Item>
        <Menu.Item>
          <a className="user_left"
            style={{
              margin: 10,
              padding: 5

            }}
            size="small"
            onClick={() => this.alterPsd()}

          >修改密码</a>
        </Menu.Item>
      </Menu>
    );
    return (
      <div>
        <Row type="flex">
          <Col xs={2} sm={2} md={4} lg={4} justify="start" id='user' style={{ color: 'blue', fontSize: '22px', textShadow: '5' }}>
            天刀DKP后台管理系统
            </Col>
          <Col xs={{ span: 4, offset: 14 }} sm={{ span: 4, offset: 14 }} md={{ span: 4, offset: 14 }} lg={{ span: 4, offset: 16 }} justify="end">
            <Dropdown.Button size='large' onClick={this.handleButtonClick} overlay={menu} type="ghost">
              {username}
            </Dropdown.Button>
          </Col>
        </Row>
        <Row>
          <Col span='4'>
            <Menu onClick={this.handleClick}
              defaultOpenKeys={['sub4']}
              selectedKeys={['/adminList']}
              mode="inline"
            >
              <SubMenu key="sub1" title={<span><Icon type="mail" /><span>用户管理</span></span>}>
                <Menu.Item key="/user"
                  onClick={() => {
                    this.props.history.push('/user')
                  }}
                >用户列表</Menu.Item>
                <Menu.Item key="/adduser"
                  onClick={() => {
                    this.props.history.push('/adduser')
                  }}
                >添加用户</Menu.Item>
              </SubMenu>
              <SubMenu key="sub2" title={<span><Icon type="appstore" /><span>奖品管理</span></span>}>
                <Menu.Item key="/object"
                  onClick={() => {
                    this.props.history.push('/object')
                  }}
                >奖品列表</Menu.Item>
                <Menu.Item key="/addobj"
                  onClick={() => {
                    this.props.history.push('/addobj')
                  }}
                >添加奖品</Menu.Item>
                <Menu.Item key="/alterFen"
                  onClick={() => {
                    this.props.history.push('/alterFen')
                  }}
                >积分消耗</Menu.Item>
              </SubMenu>
              <SubMenu key="sub3" title={<span><Icon type="setting" /><span>获奖信息</span></span>}>
                <Menu.Item key="/ownobj"
                  onClick={() => {
                    this.props.history.push('/ownobj')
                  }}
                >获奖信息</Menu.Item>
              </SubMenu>
              <SubMenu key="sub4" title={<span><Icon type="setting" /><span>管理员管理</span></span>}>
                <Menu.Item key="/adminList"
                  onClick={() => {
                    this.props.history.push('/adminList')
                  }}
                >管理员列表</Menu.Item>
                <Menu.Item key="/addAdmin"
                  onClick={() => {
                    this.props.history.push('/addAdmin')
                  }}
                >添加管理员</Menu.Item>
              </SubMenu>
            </Menu>
          </Col>
          <Col span="10">
            <Breadcrumb>
              <Breadcrumb.Item>管理员管理</Breadcrumb.Item>
              <Breadcrumb.Item><a href="/adminList">管理员列表</a></Breadcrumb.Item>
            </Breadcrumb>
            <Table
              pagination={pagination}
              columns={
                [
                  // { title: '奖品编号', width: 100, dataIndex: 'id', key: 'id' },
                  { title: '管理员昵称', width: 40, dataIndex: 'username', key: 'username' },
                  {
                    title: '操作',
                    key: 'operation',
                    fixed: 'right',
                    width: 30,
                    render: (text, record, index) => {
                      return (
                        <Button size='small' type="primary" name={record.username} onClick={() => { this.delData(record.username) }}>删除</Button>
                      )
                    },
                  },
                ]
              }
              rowKey={record => record.obj}
              dataSource={this.state.object}
            // scroll={{ x: 1200,y:500 }}
            />

          </Col>
        </Row>



      </div>
    )
  }

}
export default adminList;