class User extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: [],
      history: 0
    }
  }
  getInitialState = () => {
    return {
      ModalText: '对话框的内容',
      visible: false,
    };
  }
  showModal = (e) => {
    localStorage.setItem('cur', e.history);
    console.log(e.history);
    console.log(this.state.history);
    this.setState({
      visible: true,
      history: e.history,
      count: e.count,
      openid: e.openid,
    });
    console.log(this.state.history)
  }
  setUserInfo(event, key) {
    let obj = {};
    obj[key] = event.target.value;
    this.setState(obj);
    console.log(this.state.obj)
  }
  handleOk = (e) => {
    // const { getFieldProps } = this.props.form;
    let his = localStorage.getItem('cur');
    let cur = this.state.history - his;
    console.log(cur);
    console.log(this.state);
    this.setState({
      visible: false,
      confirmLoading: false,
    });
    // setTimeout(() => {
    //   this.setState({
    //     visible: false,
    //     confirmLoading: false,
    //   });
    // }, 1000);
    let data = {
      openid: this.state.openid,
      count: cur,//现在的积分减去修改之后的积分
      reason: this.state.reason,
      mount: this.state.count//出勤次数
    }
    fetch('https://www.sevenhero.cn:2050/detail', {
      method: 'POST',

      headers: {
        'content-type': 'text/plain'
      },
      body: JSON.stringify(data)
    }).then(async res => {
      if (!res.ok) {
        let errtext = await res.text()
        throw new Error(`${res.status} : ${errtext}`)//错误的状态码和内容
      } else {
        //  return res//成功后的返回值
        return;
      }

    }).then(d => {
      //'提交成功'
      
      this.getData();
      this.setState({
        gamename: '',
        orgation: '',
        history: '',
        count: '',
      })
      // this.props.history.push('/adduser')

    }).catch(err => {
      alert(err.message)
    })
    console.log(this.state.history);

  }
  handleCancel = () => {
    console.log('点击了取消');
    console.log(this.state.history)
    this.setState({
      visible: false,
    });
    // console.log(this.state.history)
  }

  getData = () => {
    fetch('https://www.sevenhero.cn:2050/users').then(res => {
      if (!res.ok) {
        throw new Error(`${res.status} : ${res.statusText}`)
      } else {
        return res.json()
      }
    }).then(d => {
      console.log(d);
      this.setState({
        user: d
      })
      // console.log(this.state.user)
    }).catch(err => {
      console.error(err)
    })
  }
  delData = (e) => {
    if (window.confirm('确定删除？')) {
      // let id = e.target.name;
      console.log(e);
      // console.log(id);
      fetch('https://www.sevenhero.cn:2050/users/' + e, {
        method: 'DELETE',
      }).then(async res => {

        if (!res.ok) {
          let errtext = await res.text()
          throw new Error(`${res.status} : ${errtext}`)
        } else {
          //  return res//成功后的返回值
        }

      }).then(d => {
        //'提交成功'
        alert('删除成功')
        this.getData();

      }).catch(err => {
        alert('错误')
      })
    }
  }
  componentDidMount = () => {//在render执行之后就执行，且只执行一次
    this.getData();

  }
  componentDidUpdate = () => {

  }
  handleButtonClick = (e) => {
    console.log('click left button', e);
  }

  handleMenuClick = (e) => {
    console.log('click', e);
  }
  changeHistory = (e) => {

    this.setState({
      history: e
    })
  }
  changeCount = (e) => {
    this.setState({
      count: e
    })
  }
  changeReason = (e) => {
    this.setState({
      reason: e.target.value
    })
  }
  handleSubmit = (e) => {
    console.log('click', e);
  }
  getInitialState = () => {
    return {
      current: '1',
      openKeys: [],
    };
  }
  handleClick = (e) => {
    console.log('click ', e);
    this.setState({
      current: e.key,
      openKeys: e.keyPath.slice(1),
    });
  }
  onToggle = (info) => {
    this.setState({
      openKeys: info.open ? info.keyPath : info.keyPath.slice(1),
    });
  }
  alterPsd=()=>{
    var name = prompt("请输入密码", ""); //将输入的内容赋给变量 name ，
    console.log(name)
    fetch('https://www.sevenhero.cn:2050/upadm', {
      method: 'POST',
      headers: {
        'content-type': 'text/plain'
      },
      body: JSON.stringify(name)
    }).then(async res => {

      if (!res.ok) {
        let errtext = await res.text()
        throw new Error(`${res.status} : ${errtext}`)
      } else {
        //  return res//成功后的返回值
      }

    }).then(d => {
      //'提交成功'
      localStorage.removeItem('token')
      localStorage.removeItem('userinfo')
      this.props.history.push('/login');

    }).catch(err => {
      alert('错误')
    })
   
  }
  render() {
    const username = JSON.parse(localStorage.getItem('userinfo')).username
    const pagination = {
      total: this.state.user.length,
      showSizeChanger: true,
      showTotal(total) { return `共 ${total} 条` },
      onShowSizeChange(current, pageSize) {
        console.log('Current: ', current, '; PageSize: ', pageSize);
      },
      onChange(current) {
        console.log('Current: ', current);
      },
    };
    const columns = [
      { title: '游戏昵称',  dataIndex: 'gamename', key: 'gamename',width:15},
      { title: '工会名称',  dataIndex: 'orgation', key: 'orgation',width:15 },
      { title: '历史积分', dataIndex: 'history', key: 'history',width:15 },
      { title: '当前积分', dataIndex: 'current', key: 'current',width:15 },
      { title: '出勤次数', dataIndex: 'count', key: 'count',width:15 },
      { title: '可抽奖数', dataIndex: 'can', key: 'can',width:15 },
      {
        title: '操作',
        key: 'operation',
        // fixed: 'right',
        width: 15,
        render: (text, record, index) => {
          return (
            <div>
              <Button type="primary" size='small' name='{record.gamename}' onClick={() => this.showModal(record)}>编辑</Button>
            </div>

          )
        },
      },
      {
        title: '删除',
        key: 'operation',
        // fixed: 'right',
        width: 15,
        render: (text, record, index) => {
          // console.log(record.gamename);//这里就可以拿到数据
          return (
            <Button size='small' type='ghost' style={{ marginRight: '5px' }} onClick={() => this.delData(record.openid)}>删除</Button>
          )
        },
      },
    ]
    const menu = (
      <Menu onClick={this.handleMenuClick}>
        <Menu.Item key="1">
          <a className="user_left"
          size='small'
            style={{
              margin:10,
              padding:5
            }}
            onClick={() => {
              if (window.confirm('确定退出？')) {
                localStorage.removeItem('token')
                localStorage.removeItem('userinfo')
                localStorage.removeItem('state')

                this.props.history.push('/login');

              }
            }}
          >退出账号</a>
          
        </Menu.Item>
        <Menu.Item>
        <a className="user_left"
            style={{
              margin:10,
              padding:5

            }}
            size="small"
            onClick={() => this.alterPsd()}
          >修改密码</a> 
        </Menu.Item>
        

      </Menu>
    );
    return (
      <div>
        <div>
          <Row type="flex">
            <Col xs={2} sm={2} md={4} lg={4} justify="start" id='user' style={{ color: 'blue', fontSize: '22px',textShadow:'5' }}>
              天刀DKP后台管理系统
            </Col>
            <Col xs={{ span: 4, offset: 14 }} sm={{ span: 4, offset: 14 }} md={{ span: 4, offset: 14 }} lg={{ span: 4, offset: 16 }} justify="end">
              <Dropdown.Button  size='large' onClick={this.handleButtonClick} overlay={menu} type="ghost">
               {username}
              </Dropdown.Button>
            </Col>
          </Row>


          <Row>

            <Col xs={2} sm={2} md={4} lg={4} >
              <Menu onClick={this.handleClick}
                defaultOpenKeys={['sub1']}
                selectedKeys={['/user']}
                mode="inline"
              >
                <SubMenu key="sub1" title={<span><Icon type="mail" /><span>用户管理</span></span>}>
                  <Menu.Item key="/user"
                    onClick={() => {
                      this.props.history.push('/user')
                    }}
                  >用户列表</Menu.Item>
                  <Menu.Item key="/adduser"
                    onClick={() => {
                      this.props.history.push('/adduser')
                    }}
                  >添加用户</Menu.Item>
                </SubMenu>
                <SubMenu key="sub2" title={<span><Icon type="appstore" /><span>奖品管理</span></span>}>
                  <Menu.Item key="/object"
                    onClick={() => {
                      this.props.history.push('/object')
                    }}
                  >奖品列表</Menu.Item>
                  <Menu.Item key="/addobj"
                    onClick={() => {
                      this.props.history.push('/addobj')
                    }}
                  >添加奖品</Menu.Item>
                   <Menu.Item key="/alterFen"
                    onClick={() => {
                      this.props.history.push('/alterFen')
                    }}
                  >积分消耗</Menu.Item>
                </SubMenu>
                <SubMenu key="sub3" title={<span><Icon type="setting" /><span>获奖信息</span></span>}>
                  <Menu.Item key="/ownobj"
                    onClick={() => {
                      this.props.history.push('/ownobj')
                    }}
                  >获奖信息</Menu.Item>
                </SubMenu>
                <SubMenu key="sub4" title={<span><Icon type="setting" /><span>管理员管理</span></span>}>
                  <Menu.Item key="/adminList"
                    onClick={() => {
                      this.props.history.push('/adminList')
                    }}
                  >管理员列表</Menu.Item>
                  <Menu.Item key="/addAdmin"
                  onClick={() => {
                    this.props.history.push('/addAdmin')
                  }}
                >添加管理员</Menu.Item>
                </SubMenu>
              </Menu>
            </Col>
            <Col xs={20} sm={20} md={20} lg={20} >
              <Breadcrumb>
                <Breadcrumb.Item>用户信息</Breadcrumb.Item>
                <Breadcrumb.Item><a href="/user">用户列表</a></Breadcrumb.Item>
              </Breadcrumb>
              <Table
                columns={columns}
                dataSource={this.state.user} rowKey={record => record.openid}
                scroll={{ x: 1200, y: 600 }}
                pagination={pagination}

              />

              <Modal
                title="编辑信息"
                visible={this.state.visible}
                onOk={() => this.handleOk()}
                confirmLoading={this.state.confirmLoading}
                onCancel={this.handleCancel}
                okText='提交'
                cancelText='取消'
              >

                <Form horizontal="true" onSubmit={this.handleSubmit} >
                  <FormItem
                    label="修改积分"
                    labelCol={{ span: 8 }}
                    wrapperCol={{ span: 10 }}
                  >
                    <InputNumber step='10' value={this.state.history} min='0'
                      onChange={(event) => { this.changeHistory(event) }}
                      size='large'
                      style={{ width: 100 }}
                    // onInput={(event) => {
                    //   this.setUserInfo(event, 'history')
                    // }}
                    />
                    <span className="ant-form-text">分</span>
                  </FormItem>
                  <FormItem
                    label='修改积分原因'
                    labelCol={{ span: 8 }}
                    wrapperCol={{ span: 10 }}

                  >
                    <Input type="textarea" rows={6}
                      onChange={(event) => { this.changeReason(event) }}
                      placeholder='请输入'
                      autosize
                      height='40'
                    />
                  </FormItem>
                  <FormItem
                    label="修改出勤次数"
                    labelCol={{ span: 8 }}
                    wrapperCol={{ span: 10 }}
                  >
                    <InputNumber  value={this.state.count} min='0'
                      // onChange={()=>{this.changeNumber()}}
                      size='large'
                      style={{ width: 100 }}
                      onChange={(event) => { this.changeCount(event) }}
                    />
                    <span className="ant-form-text">次</span>
                  </FormItem>
                </Form>
              </Modal>

            </Col>
          </Row>

        </div>



      </div>
    )
  }
 
}
export default User
