import React, { Component } from 'react'
import {
  Menu, Dropdown, Form, Select, InputNumber, DatePicker, TimePicker, Switch, Radio,
  Cascader, Slider, Col, Upload, Input, Modal, Row, Tabs, Table, Breadcrumb
} from 'antd';
import { Icon, Button } from 'antd-mobile';
const FormItem = Form.Item;
const SubMenu = Menu.SubMenu;
const TabPane = Tabs.TabPane;
const Option = Select.Option;
// 添加用户
class addUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      gamename: '',
      orgation: '',
      history: '',
      count: '',
      can: 5

    }
  }
  setUserInfo(event, key) {
    let obj = {};
    obj[key] = event.target.value;
    this.setState(obj);
  }
  handleSubmit(e) {
    console.log(e);
  }
  addname = (e) => {
    this.setState({
      nname: e.target.value
    }
    )
  }
  addHis = (e) => {
    this.setState({
      history: e
    }
    )
  }
  addOrg = (e) => {
    this.setState({
      orgation: e.target.value
    }
    )
  }
  addCount = (e) => {
    this.setState({
      count: e
    }
    )
  }

  putData = () => {
    let data = {
      gamename: this.state.nname,
      history: this.state.history,
      orgation: this.state.orgation,
      count: this.state.count
    }
    console.log(data)
    fetch('https://www.sevenhero.cn:2050/users', {
      method: 'POST',
      headers: {
        'content-type': 'text/plain'
      },
      body: JSON.stringify(data)
    }).then(async res => {

      if (!res.ok) {
        let errtext = await res.text()
        throw new Error(`${res.status} : ${errtext}`)//错误的状态码和内容
      } else {
        //  return res//成功后的返回值
        return;
      }

    }).then(d => {
      //'提交成功'
      alert('用户添加成功')
      this.setState({
        name: '',
        orgation: '',
        history: '',
        count: '',
      })
      // this.props.history.push('/adduser')

    }).catch(err => {
      alert(err.message)
    })
  }
  getInitialState = () => {
    return {
      current: '1',
      openKeys: [],
    };
  }
  handleClick = (e) => {
    console.log('click ', e);
    this.setState({
      current: e.key,
      openKeys: e.keyPath.slice(1),
    });
  }
  onToggle = (info) => {
    this.setState({
      openKeys: info.open ? info.keyPath : info.keyPath.slice(1),
    });
  }
  alterPsd=()=>{
    var name = prompt("请输入密码", ""); //将输入的内容赋给变量 name ，
    console.log(name)
    fetch('https://www.sevenhero.cn:2050/upadm', {
      method: 'POST',
      headers: {
        'content-type': 'text/plain'
      },
      body: JSON.stringify(name)
    }).then(async res => {

      if (!res.ok) {
        let errtext = await res.text()
        throw new Error(`${res.status} : ${errtext}`)
      } else {
        //  return res//成功后的返回值
      }

    }).then(d => {
      //'提交成功'
      localStorage.removeItem('token')
      localStorage.removeItem('userinfo')
      this.props.history.push('/login');

    }).catch(err => {
      alert('错误')
    })  
  }
  render() {
    const username = JSON.parse(localStorage.getItem('userinfo')).username

    const menu = (
      <Menu onClick={this.handleMenuClick}>
        <Menu.Item key="1">
        <a className="user_left"
          size='small'
            style={{
              margin:10,
              padding:5
            }}
            onClick={() => {
              if (window.confirm('确定退出？')) {
                localStorage.removeItem('token')
                localStorage.removeItem('userinfo')
                localStorage.removeItem('state')
                this.props.history.push('/login');

              }
            }}
          >退出账号</a>
          
        </Menu.Item>
        <Menu.Item>
        <a className="user_left"
            style={{
              margin:10,
              padding:5

            }}
            size="small"
            onClick={() => this.alterPsd()}

          >修改密码</a> 
        </Menu.Item>
      </Menu>
    );

    return (

      <div>
        <div>
          <Row type="flex">
            <Col xs={2} sm={2} md={4} lg={4} justify="start" id='user' style={{ color: 'blue', fontSize: '22px', textShadow: '5' }}>
              天刀DKP后台管理系统
            </Col>
            <Col xs={{ span: 4, offset: 14 }} sm={{ span: 4, offset: 14 }} md={{ span: 4, offset: 14 }} lg={{ span: 4, offset: 16 }} justify="end">
              <Dropdown.Button size='large' onClick={this.handleButtonClick} overlay={menu} type="ghost">
                {username}
              </Dropdown.Button>
            </Col>
          </Row>
          <Row>
            <Col xs={4} sm={4} md={4} lg={4} >
              <Menu onClick={this.handleClick}
                // style={{ width: 240 }}
                defaultOpenKeys={['sub1']}
                selectedKeys={['/adduser']}
                mode="inline"
              >
                <SubMenu key="sub1" title={<span><Icon type="mail" /><span>用户管理</span></span>}>
                  <Menu.Item key="/user"
                    onClick={() => {
                      this.props.history.push('/user')
                    }}
                  >用户列表</Menu.Item>
                  <Menu.Item key="/adduser"
                    onClick={() => {
                      this.props.history.push('/adduser')
                    }}
                  >添加用户</Menu.Item>
                </SubMenu>
                <SubMenu key="sub2" title={<span><Icon type="appstore" /><span>奖品管理</span></span>}>
                  <Menu.Item key="/object"
                    onClick={() => {
                      this.props.history.push('/object')
                    }}
                  >奖品列表</Menu.Item>
                  <Menu.Item key="/addobj"
                    onClick={() => {
                      this.props.history.push('/addobj')
                    }}
                  >添加奖品</Menu.Item>
                  <Menu.Item key="/alterFen"
                    onClick={() => {
                      this.props.history.push('/alterFen')
                    }}
                  >积分消耗</Menu.Item>
                </SubMenu>
                <SubMenu key="sub3" title={<span><Icon type="setting" /><span>获奖信息</span></span>}>
                  <Menu.Item key="/ownobj"
                    onClick={() => {
                      this.props.history.push('/ownobj')
                    }}
                  >获奖信息</Menu.Item>
                </SubMenu>
                <SubMenu key="sub4" title={<span><Icon type="setting" /><span>管理员管理</span></span>}>
                  <Menu.Item key="/adminList"
                    onClick={() => {
                      this.props.history.push('/adminList')
                    }}
                  >管理员列表</Menu.Item>
                  <Menu.Item key="/addAdmin"
                    onClick={() => {
                      this.props.history.push('/addAdmin')
                    }}
                  >添加管理员</Menu.Item>
                </SubMenu>
              </Menu>
            </Col>

            <Col xs={20} sm={20} md={20} lg={20} >
              <Breadcrumb >
                <Breadcrumb.Item>用户信息</Breadcrumb.Item>
                <Breadcrumb.Item><a href="/adduser">添加用户</a></Breadcrumb.Item>
              </Breadcrumb>
              <Form horizontal="true" onSubmit={() => this.handleSubmit()}
                style={{ marginTop: '5%' }}
              >

                <FormItem
                  id="control-input"
                  label="用户昵称"
                  labelCol={{ span: 6 }}
                  wrapperCol={{ span: 6 }}

                >
                  <Input id="control-input" onChange={(e) => this.addname(e)} value={this.state.name} />
                </FormItem>
                <FormItem
                  id="control-input"
                  label="历史积分"
                  labelCol={{ span: 6 }}
                  wrapperCol={{ span: 6 }}
                >
                  <InputNumber min={0} id="control-input" onChange={(e) => this.addHis(e)} value={this.state.history} />
                </FormItem>
                <FormItem
                  id="control-input"
                  label="用户工会"
                  labelCol={{ span: 6 }}
                  wrapperCol={{ span: 6 }}
                >
                  <Input type='text' id="control-input" onChange={(e) => this.addOrg(e)} value={this.state.orgation} />
                </FormItem>
                <FormItem
                  id="control-input"
                  label="出勤次数"

                  labelCol={{ span: 6 }}
                  wrapperCol={{ span: 6 }}
                >
                  <InputNumber min={0} id="control-input" onChange={(e) => this.addCount(e)} value={this.state.count} />
                </FormItem>
                <FormItem wrapperCol={{ span: 4, offset: 6 }} style={{ marginTop: 24 }}>
                  <Button size='small' type="primary"
                    onClick={() => {
                      //  console.log(this.state);
                      this.putData();
                    }}
                  >添加</Button>
                </FormItem>
              </Form>
            </Col>
          </Row>

        </div>

      </div>
    )
  }
}

export default addUser
