import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'

class Left extends Component {
    render() {
        return (
            <div
            style={{
                width: '20%',
                height: '1200px',
                border: '1px solid black',
                backgroundColor:'	LightSteelBlue'
            }}
            >
                 <NavLink activeClassName='active' exact to='/user' id='act_left'>用户信息</NavLink>
                <NavLink activeClassName='active' to='/object' id='arts_left'>奖品设置</NavLink>
                <NavLink activeClassName='active' to='/ownobj' id='com_left'>获奖详情</NavLink>
                
            </div>
        )
    }
}

export default Left
