import React, { Component } from 'react'

class check extends Component {
    getData=(username,password,callback)=>{
        console.log(username);
        let loginfo={
          username:username,
          password:password
        }
        console.log(loginfo)
        fetch('https://www.sevenhero.cn:2050/login', {
         method : 'POST',
         headers : {
           'content-type' : 'text/plain'
         },
         body : JSON.stringify(loginfo)
       }).then(res => {
         if (!res.ok) {
           throw new Error(`${res.status} : ${res.statusText}`)
         } else {
           return res.json()
         }
       }).then(d => {
             console.log(d);
             localStorage.setItem('token',d.token);
             localStorage.setItem('userinfo',JSON.stringify(d.userinfo))
            //  localStorage.setItem('state',d.state)
             callback();

         }).catch(err => {
           alert('用户名或密码错误')
         })
   }  
   login(username, password, callback) {
       this.getData(username,password,callback);
   }
}

export default check
