import React, { Component } from 'react'
import './User.css'
import check from './check';
import { Form, Input, Button, Checkbox } from 'antd';
import { Icon } from 'antd-mobile';
const FormItem = Form.Item;

// 登录页

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
            // state : JSON.parse(localStorage.getItem('userinfo')).state

        }
    }
    handleSubmit(e) {
        e.preventDefault();
        console.log('收到表单值：', this.props.form.getFieldsValue());
    }
    componentDidMount = () => {
        console.log('s')

        if (localStorage.getItem('token')) {
           
            try {
                let u = localStorage.getItem('userinfo');
                let state = JSON.parse(localStorage.getItem('userinfo')).state
                console.log(state)
                localStorage.setItem('state',state)
                console.log(localStorage.getItem('state'));

                if (u) {
                    let uinfo = JSON.parse(u)
                    if (uinfo.expires + uinfo.time > Date.now()) {
                        if(state == 1){
                            console.log(state);
                            this.props.history.push('/user');
                        }
                        else{
                            console.log(state)
                            this.props.history.push('/spuser');
                        }
                    }

                }
            } catch (err) {
                console.error(err)
            }
        }

    }
    setUserInfo(event, key) {
        let obj = {};
        obj[key] = event.target.value;
        this.setState(obj);
    }
    render() {
        // const { getFieldProps } = this;
        // let state = JSON.parse(localStorage.getItem('userinfo'))
        return (

            <div >
                <div >
                <h1
                    style={{
                        marginLeft: '25%',
                        color: 'black',
                        fontSize: '3rem'

                    }}
                >天刀DKP后台管理</h1>
                
                <Form
                    horizontal="true"
                    style={{
                        width: '30%',
                        height: '35%',
                        marginTop: '10%',
                        marginLeft: '33%',
                        // border:'1px solid black',
                        // backgroundColor: '#F5F5F5',
                        fontSize: '1.3rem',

                    }}
                >
                    <FormItem
                        label="账户"
                    >
                           <i className='iconfont icon-dianpu'></i><Input placeholder="请输入账户名"
                            onInput={(event) => {
                                this.setUserInfo(event, 'username');
                            }}
                        >
                        </Input>

                    </FormItem>
                    <FormItem
                        label="密码"
                        icon={
                            <i className='iconfont iconmima'></i>                           
                        }   
                    >   
                        <Input type="password" placeholder="请输入密码"
                            onInput={(event) => {
                                this.setUserInfo(event, 'password')
                            }}
                            
                        // {...getFieldProps('password')}
                        >
                        </Input>
                    </FormItem>
                    <FormItem

                     >

                        <Button type="primary" htmlType="submit"
                            style={{ 
                                marginLeft:'40%'
                             }}
                            onClick={() => {
                                let p = new check();
                                p.login(this.state.username, this.state.password, () => {
                                    // localStorage.setItem('name',`${this.state.username}`);
                                    // 登录成功时，跳转页面
                                    if(JSON.parse(localStorage.getItem('userinfo')).state == 1){
                                        this.props.history.push('/user');
                                    }
                                    else{
                                        this.props.history.push('/spuser')
                                    }
                                    
                                });

                            }

                            }
                        >登录</Button>
                    </FormItem>


                </Form>
              
                </div>
              
            </div>
        )
    }
}
// Demo = Form.create()(Demo);
export default Login
