import userEvent from '@testing-library/user-event';
import React from 'react';
import { HashRouter as Router, Redirect, Route, Switch } from 'react-router-dom';
import addObj from './addObj';
import spaddObj from './spaddObj';
import addUser from './addUser';
import spaddUser from './spaddUser';
import Login from './Login';
import Ownobj from './Ownobj';
import spOwnobj from './spOwnobj';
import User from './User';
import spUser from './spUser';
import Object1 from './Object1';
import spObject1 from './spObject1';
import AlterFen from './AlterFen';
import spAlterFen from './spAlterFen';
import AddAdmin from './addAdmin'
import AdminList from './adminList';


const App = () => {
	return <Router>

		<Switch>
			{/*登录  */}
			<Route path='/login' component={Login} />
			{/*  用户*/}
			<Route path='/user' component={User} />
			<Route path='/spuser' component={spUser} />

			{/*添加用户  */}
			<Route path='/adduser' component={addUser} />
			<Route path='/spadduser' component={spaddUser} />

			{/* 奖品 */}
			<Route path='/object' component={Object1} />
			<Route path='/spobject' component={spObject1} />

			{/*获得 奖品 */}
			<Route path='/addobj' component={addObj} />
			<Route path='/spaddobj' component={spaddObj} />

			{/*  获得奖品*/}
			<Route path='/ownobj' component={Ownobj} />
			<Route path='/spownobj' component={spOwnobj} />

			<Route path='/alterFen' component={AlterFen} />
			<Route path='/spalterFen' component={spAlterFen} />

			<Route path='/addAdmin' component={AddAdmin} />
			<Route path='/adminList' component={AdminList} />

		</Switch>
		<Redirect to='/login' />
	</Router>
}
export default App;

















