import React, { Component } from 'react'
import { Tabs, Select } from 'antd';
import { Menu, Dropdown, Table,Row,Col ,Breadcrumb} from 'antd';
import { Icon, Grid, Button } from 'antd-mobile';
const TabPane = Tabs.TabPane;
const SubMenu = Menu.SubMenu;
const Option = Select.Option;
// 奖品

class spObject1 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      object: [],
      port:'https://www.sevenhero.cn:2050/static/'
    }
  }
  getData = () => {
    fetch('https://www.sevenhero.cn:2050/object').then(res => {
      if (!res.ok) {
        throw new Error(`${res.status} : ${res.statusText}`)
      } else {
        return res.json()
      }
    }).then(d => {
      console.log(d);
      this.setState({
        object: d
      })
      // console.log(this.state.user)
    }).catch(err => {
      console.error(err)
    })
  }

  delData = (e) => {
    if (window.confirm('确定删除？')) {
      let data = {
        obj:e
      } 

      console.log(data);
      fetch('https://www.sevenhero.cn:2050/delobj', {
        method: 'POST',
        body:JSON.stringify(data)
      }).then(async res => {

        if (!res.ok) {
          let errtext = await res.text()
          throw new Error(`${res.status} : ${errtext}`)
        } else {
          //  return res//成功后的返回值
        }

      }).then(d => {
        //'提交成功'
        alert('删除成功')
        this.getData();

      }).catch(err => {
        alert('错误')
      })
    }
  }
  componentDidMount = () => {//在render执行之后就执行，且只执行一次
    this.getData();
  }
  getInitialState =()=> {
    return {
      current: '1',
      openKeys: [],
    };
  }
  handleClick=(e)=> {
    console.log('click ', e);
    this.setState({
      current: e.key,
      openKeys: e.keyPath.slice(1),
    });
  }
  onToggle = (info) =>{
    this.setState({
      openKeys: info.open ? info.keyPath : info.keyPath.slice(1),
    });
  }
  render() {
    const username = JSON.parse(localStorage.getItem('userinfo')).username

    const pagination = {
      total: this.state.object.length,
      showSizeChanger: true,
      showTotal(total){ return `共 ${total} 条`},
      onShowSizeChange(current, pageSize) {
        console.log('Current: ', current, '; PageSize: ', pageSize);
      },
      onChange(current) {
        console.log('Current: ', current);
      },
    };
    const menu = (
      <Menu onClick={this.handleMenuClick}>
        <Menu.Item key="1">
          <Button className="user_left"
            style={{
              marginRight: 0
            }}
            onClick={() => {
              if (window.confirm('确定退出？')) {
                localStorage.removeItem('token')
                localStorage.removeItem('userinfo')
                localStorage.removeItem('state')

                this.props.history.push('/login');

              }
            }}
          >退出账号</Button>
        </Menu.Item>


      </Menu>
    );
    return (
      <div>
       <Row type="flex">
            <Col xs={2} sm={2} md={4} lg={4} justify="start" id='user' style={{ color: 'blue', fontSize: '22px',textShadow:'5' }}>
              天刀DKP后台管理系统
            </Col>
            <Col xs={{ span: 4, offset: 14 }} sm={{ span: 4, offset: 14 }} md={{ span: 4, offset: 14 }} lg={{ span: 4, offset: 16 }} justify="end">
              <Dropdown.Button  size='large' onClick={this.handleButtonClick} overlay={menu} type="ghost">
                {username}
              </Dropdown.Button>
            </Col>
          </Row>
        <Row>
          <Col span='4'>
            <Menu onClick={this.handleClick}
              defaultOpenKeys={['sub2']}
              selectedKeys={['/spobject']}
              mode="inline"
            >
              <SubMenu key="sub1" title={<span><Icon type="mail" /><span>用户管理</span></span>}>
                <Menu.Item key="/spuser"
                  onClick={() => {
                    this.props.history.push('/spuser')
                  }}
                >用户列表</Menu.Item>
                <Menu.Item key="/spadduser"
                  onClick={() => {
                    this.props.history.push('/spadduser')
                  }}
                >添加用户</Menu.Item>
              </SubMenu>
              <SubMenu key="sub2" title={<span><Icon type="appstore" /><span>奖品管理</span></span>}>
                <Menu.Item key="/spobject"
                  onClick={() => {
                    this.props.history.push('/spobject')
                  }}
                >奖品列表</Menu.Item>
                <Menu.Item key="/spaddobj"
                  onClick={() => {
                    this.props.history.push('/spaddobj')
                  }}
                >添加奖品</Menu.Item>
                 <Menu.Item key="/spalterFen"
                    onClick={() => {
                      this.props.history.push('/spalterFen')
                    }}
                  >积分消耗</Menu.Item>
              </SubMenu>
              <SubMenu key="sub3" title={<span><Icon type="setting" /><span>获奖信息</span></span>}>
                <Menu.Item key="/spownobj"
                  onClick={() => {
                    this.props.history.push('/spownobj')
                  }}
                >获奖信息</Menu.Item>
              </SubMenu>
            </Menu>
          </Col>
          <Col span="20">
          <Breadcrumb>
                <Breadcrumb.Item>奖品管理</Breadcrumb.Item>
                <Breadcrumb.Item><a href="/spobject">奖品列表</a></Breadcrumb.Item>
              </Breadcrumb>
            <Table
              pagination={pagination}
              columns={
                [
                  // { title: '奖品编号', width: 100, dataIndex: 'id', key: 'id' },
                  { title: '奖品名字', width: 100, dataIndex: 'obj', key: 'obj' },
                  { title: '奖品图片', dataIndex: 'img', key: 'img', width: 150 ,render:(text,record,index)=>{
                    return (
                      <img style={{width:'50px',height:'50px'}} src={this.state.port+record.img}></img>
                    )
                  }},
                  { title: '奖品数量', dataIndex: 'count', key: 'count', width: 150 },
                  { title: '中奖概率', dataIndex: 'probability', key: 'probability', width: 150 ,
                render:(text,record,index)=>{
                  return (
                    <p>{record.probability}%</p>
                  )
                }},
                  {
                    title: '操作',
                    key: 'operation',
                    fixed: 'right',
                    width: 100,
                    render: (text, record, index) => {
                      return (
                        <Button size='small'  type="primary"  style={{ marginRight: '5px' }} name={record.obj} onClick={() => { this.delData(record.obj) }}>删除</Button>
                      )
                    },
                  },
                ]
              }
              rowKey={record => record.obj}
              dataSource={this.state.object}
              // scroll={{ x: 1200,y:500 }}
            />

          </Col>
        </Row>



      </div>
    )
  }
}
export default spObject1;